@mixin desktop {
  @media screen and (max-width: 1136px) {
    @content;
  }
}

@mixin tablets {
  @media screen and (max-width: 1023px) {
    @content;
  }
}

@mixin phones {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin retina {
  @media screen and (min-resolution: 2dppx) {
    @content;
  }
}
